import React from 'react'

const MapComponent = ({iframeLink}) => {
  return (
    <div className="flex justify-center mt-10">
      <iframe
        className='w-[90vw] h-[35vh] md:h-[70vh]'
        src={iframeLink}
        allowFullScreen=""
        loading="lazy"
        title="Location_Map"
      ></iframe>
    </div>
  );
};

export default MapComponent;
